@import 'variables.module.scss';

.wrapper {
  // height: 140px;
  width: 100%;
  position: absolute;
  color: $white;
  &:after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 140px;
    background: $gray-bg-dark;
    opacity: 0;
    transition: $ease-200;
    z-index: 100;
    @media only screen and (max-width: $break-medium) {
      height: 64px;
    }
  }
  &.sticky {
    &:after {
      opacity: 0.75;
    }
  }
  > div {
    position: fixed;
    z-index: 101;
    display: flex;
    align-items: center;
    width: 100vw;
    height: 140px;
    padding: 0 32px;
    box-sizing: border-box;
    @media only screen and (max-width: $break-medium) {
      justify-content: space-around;
      padding-top: 128px;
      position: fixed;
      right: -100vw;
      top: 0;
      width: 100vw;
      height: 100vh;
      background: $gray-bg-dark;
      flex-direction: column;
      z-index: 101;
      box-sizing: border-box;
      transition: $ease-200;
      &.open {
        right: 0;
      }
    }
  }
  // &.sticky {
  //   > div {
  //     position: fixed;
  //     z-index: 10;
  //   }
  // }
  .mobile-menu {
    display: none;
    @media only screen and (max-width: $break-medium) {
      display: block;
      width: 48px;
      height: 48px;
      background-image: url('~assets/images/menu.svg');
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 24px 24px;
      background-color: transparent;
      position: fixed;
      top: 8px;
      right: 8px;
      z-index: 102;
      transition: $ease-200;
      &.open {
        background-image: url('~assets/images/close.svg');
      }
    }
  }
  .logo {
    width: 269px;
    height: 68px;
    margin-right: auto;
    background: url('~assets/images/logo.png');
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 100%;
    @media only screen and (max-width: $break-large) {
      width: 150px;
      margin-right: auto;
      background-position: 0 6px;
    }
    @media only screen and (max-width: $break-medium) {
      // width: 200px;
      position: fixed;
      top: 10px;
      left: 16px;
      background-position: 0 0;
    }
    @media only screen and (max-width: $break-small) {
      width: 130px;
      height: 50px;
      top: 16px;
    }
  }
  a {
    font-size: 16px;
    text-transform: uppercase;
    margin-left: 32px;
    text-align: center;
    word-spacing: 3px;
    @media only screen and (max-width: $break-large) {
      &:not(.button) {
        height: 36px;
        margin-left: 16px;
      }
    }
    @media only screen and (max-width: $break-medium) {
      font-size: 30px;
      margin-left: 0 !important;
      margin-bottom: 64px;
    }
    &.button {
      width: 180px;
      padding: 12px 0;
      text-transform: none;
      word-spacing: normal;
      @media only screen and (max-width: $break-large) {
        width: auto;
        padding: 12px 24px;
        // margin-left: auto;
        position: relative;
        top: -8px;
      }
      @media only screen and (max-width: $break-medium) {
        margin-top: 64px;
      }
    }
  }
}
